
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import service from "@/uilt/request";
import { orderList, recOrder,orderMsg,kclist } from "@/api/order";
export default {
components: {
Panel,
Group,
Radio,
Confirm
},
watch: {
type: function(old, news) {
this.list=[];
this.getData();
},
userid:function(old,news){

}
},
methods: {
onImgError(item, $event) {
console.log(item, $event);
},
getData() {
this.$vux.loading.show({ text: "Loading" });
var dataset = this;
debugger
if(this.type!=4)
orderList(this.pageindex, 6, this.userid, this.type)
.then(function(result) {
dataset.$vux.loading.hide();
if (result.data.length > 0) {
dataset.pageindex++;
dataset.title = " " + result.count + " ";
for (var i = 0; i < result.data.length; i++) {
var order = result.data[i];
dataset.list.push(dataset.CrateItem(order));
}
} else {
dataset.$vux.toast.show({ text: "" });
}
})
.catch(function(result) {
console.log(result);
dataset.$vux.loading.hide();
alert("");
});
else  kclist(this.pageindex, 6, this.points.lat, this.points.lon)
.then(function(result) {
debugger
dataset.$vux.loading.hide();
if (result.data.length > 0) {
dataset.pageindex++;
dataset.title = " " + result.count + " ";
for (var i = 0; i < result.data.length; i++) {
var order = result.data[i];
dataset.list.push(dataset.CrateItem(order));
}
} else {
dataset.$vux.toast.show({ text: "" });
}
})
.catch(function(result) {
console.log(result);
dataset.$vux.loading.hide();
alert("");
});
},
CrateItem(order) {
var t = {
desc: "",
meta: { source: "", date: "", other: "" },
data: null
};
t.desc =
"" + order.startlocation + "" + order.endlocation + ".";
t.meta.source = ":" + order.driverprice;
t.meta.date = order.startTime.split("+")[0].replace("T", " ");
t.data = order;
return t;
},
onConfirm(){
this.recOrderFun(this.tmp);
},
commit(item) {
console.log(item);
this.show=true;
this.tmp=item;
},
recOrderFun(item){
console.log(this.driverInfo);
var obj = {
state: 4,
driverid: this.driverInfo.userid,
driverInfo: JSON.stringify(this.driverInfo)
};
var dataset=this;
var userinfo= JSON.parse( item.data.ordersInfo);
recOrder(item.data.id, obj).then(function(data) {
if (data) {
var msg =
"" +userinfo.startingPoint +"" +
userinfo.endingPoint +"\r\n" +
dataset.driverInfo.usercall + "\r\n:" +
dataset.driverInfo.mobile;

var msg2 =
"" +userinfo.startingPoint +
"" +userinfo.endingPoint +
"\r\n" +userinfo.nickname +
"\r\n:" +
userinfo.rider +"\r\n" +
userinfo.StartTime;

dataset.$vux.toast.show({ text: '' });
orderMsg(userinfo.openid,msg);
console.log("driverInfo",dataset.driverInfo);
orderMsg(dataset.driverInfo.openid,msg2);
} else dataset.$vux.toast.show({ text: "" });
});
},
onfooter() {
this.getData();
}
},
data() {
return {
list: [],
title: "",
footer: {
title: "..."
},
pageindex: 1,
show:false,
tmp:{}
};
},
created() {
this.getData();
},
props: ["type", "userid", "driverInfo","points"]
};



